@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
*{margin:0; padding:0;}
html,body,#root,.App{width:100%;height:100%; position:relative;}
.visuallyhidden{
  width: 1px; height: 1px;
  position: absolute; border: 0; margin: -1px; padding: 0;
  clip: rect(0 0 0 0); overflow: hidden;
}
.desc{display:none;}
button{appearance: none; color:inherit; border:none; background-color:transparent;}
.App{display: grid; grid-auto-flow:column;
grid-template-columns: 1fr; grid-template-rows: 3fr 14fr 3fr;}
.material-icons { font-size:2.5rem;}
header{display:flex; place-items: center;
background-color:#2f1f8f; color:#fff;}
header h1{ text-align:center; justify-self:center; margin:0 auto;}
.global-menu{ display:flex; flex-flow:column;
  position: absolute; top:0; left:0;
  background-color:#2f1f8f; color:#fff;
  width:20%; height:100%; transition: left;
}
#menu-wrapper{transition:visibility 0.3s, left 0.3s;}
#menu-wrapper a{text-decoration: none; color:inherit; }
#menu-wrapper nav{order:2; flex:1;}
#btnCloseMenu{ place-self: flex-end; order:1;}

.isOpen.true {left:0;}
.isOpen.false { left:-1000px; visibility:hidden;}

#catchMe{
  position: absolute;
  top:50%; left:50%;
  transform:translateX(-50%) translateY(-50%);
  font-size:3em;
}